import axios from 'axios';

const apiBase = process.env.REACT_APP_API_URL;

// Add a request interceptor
axios.interceptors.request.use( config => {
  const user = JSON.parse(localStorage.getItem('userData')) || {};

  if(user && user.token){
    const token = 'Bearer ' + user.token;
    config.headers.Authorization =  token;
  }
  return config;
});

class BackendService {
  async getDashBoard() {
    return await axios.post(apiBase + "/app/LoadAssignedList");
  }

  async getScheduledList() {
    return await axios.post(apiBase + "/app/ScheduledList");
  }
  async getLoadForInspection(data) {
    return await axios.post(apiBase + "/app/LoadForInspection", data);
  }
  async InsertCheckListResults(data) {
    return await axios.post(apiBase + "/app/InsertCheckListResults", data);
  }

  async getInspectedList() {
    return await axios.post(apiBase + "/app/VisitedHistory");
  }

  async getReScheduledList() {
    return await axios.post(apiBase + "/app/ReScheduledList");
  }

  async ScheduleSiteVisit(data) {
    return await axios.post(apiBase + "/app/ScheduleSiteVisit", data);
  }

  async getPmBoard() {
    return await axios.get("/api/test/pm");
  }

  async getAdminBoard() {
    return await axios.get("/api/test/admin");
  }
}

export default new BackendService();