import React, { useEffect, useState } from 'react'
import BackendService from "../services/BackendService";
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import paginationFactory from 'react-bootstrap-table2-paginator';
import { Link, useHistory, useParams } from 'react-router-dom';
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'
import toast from 'react-hot-toast';
import config from "../config/toast";


export default function Dashboard() {
  const params = useParams()
  const history = useHistory();
  const [disable, setDisable] = useState(false);
  const [requests, setRequests] = useState([]);
  const [modalStyle, setmodalStyle] = useState({"display": "none", "padding-left": "0px;"});
  const [backdropStyle, setbackdropStyle] = useState({"display": "none"});

  // Insert params
  const [CheckList, setCheckList] = useState({
    UnitId: parseInt(params.unitId),
    PropertyTypeId: 0,
    IndexCode: '',
    Result: true,
    UCId: parseInt(params.ucId),
    Remarks: '',
    Image: ''
  });
  
  const [isFetching, setIsFetching] = useState(true);


  const openModal = (row) => {
      var PropertyTypeId = row.PropertyTypeId;
      var IndexCode = row.IndexCode;
      var Remarks = '';
      var Image = '';
      setCheckList({...CheckList, PropertyTypeId, IndexCode, Remarks, Image})
      console.log(CheckList)
      setmodalStyle({"display": "block", "padding-left": "0px;"});
      setbackdropStyle({"display": "block"});
  }
  const closeModal = (row) => {
      setmodalStyle({"display": "none", "padding-left": "0px;"});
      setbackdropStyle({"display": "none"});
  }

  const setRemarks = (e) => {
    var Remarks = e.target.value;
    setCheckList({...CheckList, Remarks});
  }

  const setResult = (e) => {
    var Result = Boolean(parseInt(e.target.value));
    setCheckList({...CheckList, Result});
  }

  const toBase64 = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });

  async function setImage(e) {
    const file = e.target.files[0];
    var Image = await toBase64(file);
    setCheckList({...CheckList, Image});
 }

  const columns = [{
    dataField: 'Action',
    text: 'ACTION',
    formatter: (cell, row, rowIndex, formatExtraData) => {
        
      return (
          <button className="btn btn-primary"
          >Insert CheckList
          </button>
      )
    },
    events: {
        onClick: (e, column, columnIndex, row, rowIndex) =>  { openModal(row) }
    }}, {
    dataField: 'UnitType',
    text: 'Uunit Type',
    sort: true
  }, {
    dataField: 'IndexCode',
    text: 'Index Code',
    sort: true
  }, {
    dataField: 'CodeName',
    text: 'Code Name',
    sort: true
  }, {
    dataField: 'MainOrSub',
    text: 'Main Or Sub',
    sort: true
  }, {
    dataField: 'Criterias',
    text: 'Criterias',
    sort: true,
    headerStyle: (colum, colIndex) => {
        return { width: '80px', textAlign: 'left' };
      }
  }, {
    dataField: 'Remarks',
    text: 'Remarks',
    sort: true
  }, {
    dataField: 'Result',
    text: 'Result',
    sort: true
  },{
    dataField: 'Image',
    text: 'Image',
    formatter: (cell, row, rowIndex, formatExtraData) => {
        
      return (
        row.Image ?  (
          <a href={row.Image} target="_blank" ><img alt src={row.Image} /></a>
        ) : ( <a></a> )

      );
    }
  }];
  
  const defaultSorted = [{
    dataField: 'name',
    order: 'desc'
  }];

  const pagination = paginationFactory({
    sizePerPage: 2,
    hideSizePerPage: true,
    prePageText: "Previous",
    nextPageText: "Next",
    withFirstAndLast: false,
    hidePageListOnlyOnePage: false
  });

  useEffect(() => {
    BackendService
      .getLoadForInspection({UnitId: parseInt(params.unitId), UCId: parseInt(params.ucId)})
      .then((response) => {
        setIsFetching(false);
        console.log(response.data.data.data);
        setRequests(JSON.parse(response.data.data.data));
      });
  }, []);

  const doPost = (e) => {
    e.preventDefault();
    setDisable(true);
    BackendService
      .InsertCheckListResults(CheckList)
      .then((response) => {
        setDisable(false);
          if (response.status === 200 && response.data.statusCode === 200) {
            toast.success('Successfully Inserted CheckList.', config);
            closeModal();
          } else {
            toast.error('Incorrect username or password. Please try again.', config);
          }
      })
      .catch((error) => {
          console.log(error)
        // Error
        if (error.response) {
            // The request was made and the server responded with a status code
            // that falls out of the range of 2xx
            // console.log(error.response.data);
            // console.log(error.response.status);
            // console.log(error.response.headers);
            toast.error("There is a problem while calling the api request.", { appearance: 'error', autoDismiss: true });
            //addToast("Incorrect username or password. Please try again.", { appearance: 'error', autoDismiss: true });
        } else if (error.request) {
            // The request was made but no response was received
            // `error.request` is an instance of XMLHttpRequest in the 
            // browser and an instance of
            // http.ClientRequest in node.js
            toast.error("Network problems happen when something disrupts the connection between your computer and the content you're trying to access.", { appearance: 'error', autoDismiss: true });
        } else {
            // Something happened in setting up the request that triggered an Error
            toast.error('Error', error.message);
        }
      });
    e.preventDefault();
  };

    return (
        <div className="container-fluid page-body-wrapper">
        <div className="main-panel">
          <div className="content-wrapper">
            <div className="row">

              <div className="col-lg-12 grid-margin stretch-card">
                <div className="card">
                  <div className="card-body">
                    <h4 className="card-title">Inspection Process</h4>
                    <div className="table-responsive">
                    { isFetching ? (
                        <Skeleton duration={3} height={30} count={50} />
                    ) : (
                    <>
                    <BootstrapTable
                        bootstrap4
                        keyField="id"
                        data={ requests }
                        striped
                        condensed
                        columns={ columns }
                        defaultSorted={ defaultSorted }
                      />
                    </>
                    ) }
                    </div>
                  </div>
                </div>
              </div>
            </div>
            
          </div>
        </div>
        <div className="modal-backdrop fade show" style={backdropStyle}></div>
        <div className="modal fade show" id="exampleModal-4" style={modalStyle} tabIndex={-1} role="dialog" aria-labelledby="exampleModalLabel-4" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel-4">Insert CheckList Results</h5>
              <button type="button" onClick={closeModal} className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">×</span>
              </button>
            </div>
            <form onSubmit={doPost}>
            <div className="modal-body">
              
                <div className="form-group">
                  <label htmlFor="recipient-name" className="col-form-label">Status:</label>
                  <select type="text" onChange={setResult} className="form-control" id="recipient-name" >
                      <option value="1">True</option>
                      <option value="0">False</option>
                  </select>
                </div>
                <div className="form-group">
                  <label htmlFor="message-text" className="col-form-label">Remarks:</label>
                  <textarea className="form-control" value={CheckList.Remarks} onInput={setRemarks}  id="message-text" defaultValue={""} />
                </div>
                <div className="form-group">
                  <label htmlFor="message-text" className="col-form-label">Choose Image</label>
                  <input type="file" onChange={setImage} className="form-control" id="input-file" defaultValue={""} />
                </div>
              
            </div>
            <div className="modal-footer">
              <button type="submit" disabled={disable} className="btn btn-success">Submit</button>
              <button type="button" onClick={closeModal} className="btn btn-light" data-dismiss="modal">Close</button>
            </div>
            </form>
          </div>
        </div>
      </div>
        </div>
    )
}
